$(".object-tab").click(function() {
    $(".object-tab").removeClass("_active")
    $(this).addClass("_active")
})

let videos = document.querySelectorAll(".video video")

videos.forEach(video => {
    if(video){
        video.addEventListener('click',function(){
            video.play();
        },false);
        video.click();
        video.play();
    }
})
